@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
:root {
    --ciano: #00eeff;
    --degradado1:#1f293a;
    --white: #ffffff;
    --black: #000000;
    --verm1:#ff0000;
    --verm2:#5f0b0b;
    --verde1:#00ff15;
    --verde2:#0a6411;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins',sans-serif;
}

body{
    display:flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #1f293a;
}

.container{
    position: relative;
    width: 256px;
    height: 256px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container span{
    position: absolute;
    left: 0;
    width: 32px;
    height: 6px;
    background: #2c4766;
    border-radius: 8px;
    transform-origin: 128px;
    transform: scale(2.2) rotate(calc(var(--i)*(360deg/50)));
    animation: blink 3s linear infinite;
    animation-delay: calc(var(--i)*(3s/50));
}

@keyframes blink {
    0% {
        background: #0ef;
    }

    25% {
        background: #2c4766;
    }
}

.login-box {
    position: absolute;
    width: 400px;
}

form {
    width: 100%;
    padding: 0 50px;
}

h2 {
    font-size: 2em;
    color: #0ef;
    text-align: center;
}

.input-box {
  position: relative;
  margin: 25px 0;
}

input {
  width: 100%;
  height: 50px;
  background: transparent;
  border: 2px solid #0ef;
  outline: none;
  border-radius: 40px;
  font-size: 1em;
  color: #fff;
  padding: 0 20px;
  transition: .5s ease;
}

input:focus,
input:not(:placeholder-shown) {
  border-color: #0ef;
}

label {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  color: #aaa;
  transition: 0.3s ease;
  background: transparent;
  padding: 0 5px;
  pointer-events: none;
}

input:focus~label,
input:not(:placeholder-shown)~label {
  top: -10px;
  font-size: 0.8em;
  background: #1f293a;
  color: #0ef;
}



.forgot-pass {
    margin: -15px 0 10px;
    text-align: center;
}

.forgot-pass a {
    font-size: .85em;
    color: #fff;
    text-decoration: none;
}

.btn3 {
    
    color: white;
    width: 100%;
    height: 45px;
    background: linear-gradient(var(--ciano),var(--degradado1));
    border: none;
    outline: none;
    border-radius: 40px;
    cursor: pointer;
    font-size: 1em;
    font-weight: 600;
}

.signup-link {
    margin: 20px 0 10px;
    text-align: center;
}

.signup-link a {
    font-size: 1em;
    color: #0ef;
    text-decoration: none;
    font-weight: 600;
}



body {
    background: var(--bg);
}
.light-theme {

    --bg: var(--degradado1);
    --fontColor: var(--white);
    --btnBg: var(--black);
    --btnFontColor: var(--white);
}
.dark-theme {
    --bg: var(--black);
    --fontColor: var(--ciano);
    --btnBg: var(--white);
    --btnFontColor: var(--black);

}

.btn2{
    color: var(--btnFontColor);
    background-color: var(--btnBg);
}
.btn2 {
    position: absolute;
    top: 20px;
    left: 1850px;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    border: none;
    color: var(--btnFontColor);
    background-color: var(--btnBg);
    cursor: pointer;
}
.btn2:focus{outline-style: none;}


.theme-switch {
    --toggle-size: 10px;
    --container-width: 5.625em;
    --container-height: 2.5em;
    --container-radius: 6.25em;
    --container-light-bg: #3D7EAE;
    --container-night-bg: #1D1F2C;
    --circle-container-diameter: 3.375em;
    --sun-moon-diameter: 2.125em;
    --sun-bg: #ECCA2F;
    --moon-bg: #C4C9D1;
    --spot-color: #959DB1;
    --circle-container-offset: calc((var(--circle-container-diameter) - var(--container-height)) / 2 * -1);
    --stars-color: #fff;
    --clouds-color: #F3FDFF;
    --back-clouds-color: #AACADF;
